<template>
  <div class="page graybg page-payments">
    <div class="bankCardsWrapper">
      <div class="bankCards">
        <van-tabs v-model="active" :before-change="handleBeforeTabsChange">
          <van-tab title="普通收付"></van-tab>
          <van-tab title="商家收付"></van-tab>
        </van-tabs>
        <div class="title" @click="$router.push(branchRouterPath('/banks'))">
          <span class="label"> 银行卡 </span>
          <span v-if="bank.list.length" class="count"> 全部 （{{ bank.list.length }}） </span>
        </div>
        <div v-if="bank.list.length" class="cards">
          <template v-for="(item, index) in bank.list">
            <div v-if="index < 2" :key="index" :class="`card bg${index}`">
              <van-image :src="$root.getImageUrl(item.logoImg)" round></van-image>
              <div class="bankInfo">
                <div class="top">
                  <span class="bankName">
                    {{ bankIdMappingName[item.bankId] }}
                  </span>
                  <span class="bankNum"> ****{{ item.bankNo.slice(-4) }} </span>
                </div>
                <div class="bankStatus">可转账</div>
              </div>
            </div>
          </template>
        </div>
        <template v-else>
          <div class="bank-empty"></div>
          <div class="empty-tip">你还未绑定银行卡</div>
        </template>
      </div>
    </div>
    <div class="payment" v-if="!hideAlipayWechat">
      <!--  @click="$router.push('/wechats')" -->
      <div class="title border_bottom_1px" @click="$router.push(branchRouterPath('/wechats'))">
        <span class="icon wechat"></span>
        <span class="label"> 微信 </span>
        <span v-if="wechat.list.length" class="count"> 全部 （{{ wechat.list.length }}） </span>
        <span v-else class="count"> 未绑定 </span>
        <van-icon name="arrow" />
      </div>
      <template v-for="(item, index) in wechat.list">
        <div v-if="index == 0" :key="index" class="paymentCard">
          <div class="icon wechat"></div>
          <div class="info">
            <div>微信真实姓名：{{ item.wxRealname | safeText }}</div>
            <div>姓名：{{ item.realName | safeRealName }}</div>
          </div>
          <van-image :src="$root.getImageUrl(item.receiptCode)" class="qr"> </van-image>
        </div>
      </template>
    </div>
    <div class="payment" v-if="!hideAlipayWechat">
      <!-- @click="$router.push('/alipays')" -->
      <div class="title border_bottom_1px" @click="$router.push(branchRouterPath('/alipays'))">
        <span class="icon ali"></span>
        <span class="label"> 支付宝 </span>
        <span v-if="ali.list.length" class="count"> 全部 （{{ ali.list.length }}） </span>
        <span v-else class="count"> 未绑定 </span>
        <van-icon name="arrow" />
      </div>
      <template v-for="(item, index) in ali.list">
        <div v-if="index == 0" :key="index" class="paymentCard">
          <div class="icon ali"></div>
          <div class="info">
            <div>支付宝账号：{{ item.alipayAccount | safeText }}</div>
            <div>姓名：{{ item.realName | safeRealName }}</div>
          </div>
          <van-image :src="$root.getImageUrl(item.receiptCode)" class="qr"> </van-image>
        </div>
      </template>
    </div>

    <div class="payment">
      <div class="title border_bottom_1px" @click="$router.push(branchRouterPath('/usdts'))">
        <span class="icon usdt"></span>
        <span class="label"> USDT收款 </span>
        <span v-if="usdt.list.length" class="count"> 全部 （{{ usdt.list.length }}） </span>
        <span v-else class="count"> 未绑定 </span>
        <van-icon name="arrow" />
      </div>
      <template v-for="(item, index) in usdt.list">
        <div v-if="index == 0" :key="index" class="paymentCard">
          <div class="icon usdt"></div>
          <div class="info">
            <div>USDT收款地址：{{ item.usdtAddress | safeText }}</div>
            <div>姓名：{{ item.realName | safeRealName }}</div>
          </div>
        </div>
      </template>
    </div>

    <van-button class="block primary" @click="$router.push(branchRouterPath('/add_payment'))">
      <van-icon name="add" />
      添加收付方式
    </van-button>
  </div>
</template>

<script>
import { cryptoPaymentsMixins } from "@/vendor/mixins";
export default {
  mixins: [cryptoPaymentsMixins],
  computed: {
    active: {
      get() {
        return this.isMerchant ? 1 : 0
      }
    },
    bank() {
      return this.payments.bank;
    },
    ali() {
      return this.payments.ali;
    },
    wechat() {
      return this.payments.wechat;
    },
    usdt() {
      return this.payments.usdt;
    },
    bankIdMappingName() {
      return this.$root.banks.reduce(
        (previous, current) => ({
          ...previous,
          [current.bankId]: current.bankName,
        }),
        {}
      );
    },
  },
  created() {
    this.$root.loadPayments();
    this.$root.updateUnReadCount();
    this.$root.$emit("root:set-topbar", ["notices", "customer-service"]);
    this.$root.$emit("root:set-layout", { bottom: true });
  },
  methods: {
    handleBeforeTabsChange(name) {
      if (name === this.active) {
        return true;
      }
      if (name === 0) {
        this.$router.replace("/payments");
      }
      if (name === 1) {
        this.$router.replace("/payments/merchant");
      }
    },
    branchRouterPath(path) {
      if (this.isMerchant) {
        path += "?m=1";
      }
      return path;
    },
  }
};
</script>

